<template>
  <div class="d-flex">

    <!-- Designer -->
    <div class="ml-2">
      <v-list class="list-designer" :active="brands">
        <div class="div-list_title">DESIGNERS</div>

        <!-- Brand Category -->
        <v-list-group :ripple="false" value="brands">
          <template v-slot:activator>
            <v-list-item-content>
              <v-list-item-title class="list-item_title">Category</v-list-item-title>
            </v-list-item-content>
          </template>

          <v-list-item v-for="(brand, index) in brands" :key="index">
            <v-list-item-content>
              <v-list-item-title class="list-subitem_title" :class="{active: brand.isActive}" @click="setBrand(index)">{{brand.name}}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list-group>
        <!-- Brand Category End -->

        <!-- Set Price -->
        <v-list-group :ripple="false" @click="scrollTop">
          <template v-slot:activator>
            <v-list-item-content>
              <v-list-item-title class="list-item_title">Price</v-list-item-title>
            </v-list-item-content>
          </template>

          <v-list-item>
            <v-list-item-content>
              <v-range-slider
                color="black"
                track-color="gray"
                track-fill-color="black"
                v-model="priceRange"
                min="15000"
                max="1000000"
                step="10000"
                @change="setPriceRange"
              />

              <div class="div-price_range">
                <div
                  v-for="(price, i) in priceRange"
                  :key="i"
                  :model-value="price"
                  @change="$set(priceRange, i, $event)"
                  v-text="`₩${price.toLocaleString('ko-KR')}`"
                />
              </div>
            </v-list-item-content>
          </v-list-item>
        </v-list-group>
        <!-- Set Price End -->

      </v-list>
    </div>
    <!-- Designer End -->

    <!-- Product List -->
    <div class="div-brands_product_list">
      <List ref="listVue" :itemWidth="itemWidth" :itemHeight="itemHeight" />
    </div>
    <!-- Product List End -->

  </div>
</template>

<script>
import List from "../components/List.vue";

export default {
  components: {
    List,
  },

  data() {
    return {
      itemWidth: 294,
      itemHeight: 294,
      brands: [
        {name: "All", isActive: true},
        {name: "A.P.C.", isActive: false},
        {name: "Advisory Board Crystals", isActive: false},
        {name: "Apostle Club", isActive: false},
        {name: "Billionaire boys club", isActive: false},
        {name: "Birkenstock", isActive: false},
        {name: "BTFL", isActive: false},
        {name: "Cold World Frozen Goods", isActive: false},
        {name: "Clae", isActive: false},
        {name: "Clarks", isActive: false},
        {name: "Craft + Foster", isActive: false},
        {name: "Druthers NYC", isActive: false},
        {name: "Evolve", isActive: false},
        {name: "Everyday Labour", isActive: false},
        {name: "G-Star", isActive: false},
        {name: "G-Shock", isActive: false},
        {name: "Herschel", isActive: false},
        {name: "Honor the Gift", isActive: false},
        {name: "Jason Markk", isActive: false},
        {name: "John Elliott", isActive: false},
        {name: "Karhu", isActive: false},
        {name: "Ksubi", isActive: false},
        {name: "LiteYear", isActive: false},
        {name: "Chinatown market", isActive: false},
        {name: "Marni", isActive: false},
        {name: "New Balance", isActive: false},
        {name: "New Era", isActive: false},
        {name: "Nudie Jeans", isActive: false},
        {name: "On Running", isActive: false},
        {name: "Planes", isActive: false},
        {name: "Penfield", isActive: false},
        {name: "Pleasures", isActive: false},
        {name: "Qilo NYC", isActive: false},
        {name: "Rains", isActive: false},
        {name: "Reigning Champ", isActive: false},
        {name: "RetroSuperFuture", isActive: false},
        {name: "Salt & Stone", isActive: false},
        {name: "Saturdays NYC", isActive: false},
        {name: "Saucony", isActive: false},
        {name: "Stampd", isActive: false},
        {name: "Stussy", isActive: false},
        {name: "Taschen", isActive: false},
        {name: "the Unbranded Brand", isActive: false},
        {name: "Wax London", isActive: false},
        {name: "Wings + Horn", isActive: false},
      ],
      curBrand: "All",
      curBrandIndex: 0,
      priceRange: [15000, 1000000], 
      curLow: 15000,
      curHigh: 1000000,
    };
  },

  methods: {
    setPriceRange(event) {
      this.curLow = event[0];
      this.curHigh = event[1];
      this.$refs.listVue.printBy(this.curBrand, event[0], event[1]);
    },

    setBrand(index) {
      this.brands[this.curBrandIndex].isActive = false;
      this.brands[index].isActive = true;
      this.curBrand = this.brands[index].name;
      this.curBrandIndex = index;

      this.$refs.listVue.printBy(this.curBrand, this.curLow, this.curHigh);

      this.scrollTop();
    },

    scrollTop() {
      window.scrollTo({top : 0, behavior: "smooth"})
    }
  },
};
</script>

<style>
.list-designer {
  margin: 33px 0 0 0;
  width: 228px;
}

.div-list_title {
  font-size: 26px;
  line-height: 31.2px;
  margin: 0 0 10px 15px;
  height: 52px;
  border-bottom: solid 1px #bbb;
}

.list-item_title {
  padding: 15px 0 15px 0;
}

.list-subitem_title {
  font-size: 14px !important;
  cursor: pointer;
}

.active {
  font-weight: bold;
}

.list-subitem_title:hover {
  color: #aaa;
}

.v-list-item {
  min-height: 25px !important;
}

.v-list-item__content {
  padding: 0;
}

.v-list-group__header::before {
  content: none;
}

.v-slider {
  cursor: pointer;
  margin: 0 8px -20px 8px;
}

.v-slider__thumb::before {
  content: none !important;
}

.div-price_range {
  display: flex;
  justify-content: space-between;
}

.div-brands_product_list {
  margin: 40px 0 0 0px;
}
</style>