<template>
  <div>

    <!-- Product List -->
    <div>
      <ul class="ul-clothing_list">
        <li class="li-clothing_list_item" v-for="i in clothingToShow" :key="i">
          <div class="div-clothing_list_item_wrapper" :style="{width: `${itemWidth}px`, height: `${itemHeight}px`}">
            <v-img :src="require(`@/assets/${clothing[i - 1].url}`)" />
            <div align="left">
              <div class="div-clothing_name">{{clothing[i - 1].name}}</div>
              <div class="div-clothing_price">₩{{clothing[i - 1].price}}</div>
            </div>
          </div>
        </li>
      </ul>
    </div>
    <!-- Product List End -->

    <!-- Load More -->
    <div v-if="clothingToShow < totalClothing" class="div-load_more_btn_wrapper" align="center">
      <v-btn class="btn-load_more" elevation="0" @click="loadMore">Load More</v-btn>
    </div>
    <!-- Load More End -->

  </div>
</template>

<script>
export default {
  props: ["itemWidth", "itemHeight"],

  data() {
    return {
      data: [],
      clothing: [],
      totalClothing: 0,
      clothingToShow: 0,
      loadDataCount: 16,
    }
  },

  mounted() {
    this.getData();
  },

  methods: {
    // 상품 데이터 가져오기
    getData() {
      this.$axios.get('/test').then((res) => {
        res.data.forEach((element) => {
          this.data.push({
            name: element.name,
            price: element.price.toLocaleString("ko-KR"),
            url: element.url,
            brand: element.brand,
            register_date: element.register_date
          });
        });

        this.clothing = this.data;
        this.totalClothing = this.clothing.length;
        this.clothingToShow = Math.min(this.loadDataCount, this.totalClothing);
      });
    },

    // 상품 데이터 추가 로드
    loadMore() {
      this.clothingToShow += Math.min(this.loadDataCount, this.totalClothing - this.clothingToShow);
    },

    // 상품 리스트 정렬
    sortBy(option) {
      if (option == "Newest") {  // Newest
        this.clothing.sort((a, b) => {
          return new Date(b.register_date).getTime() - new Date(a.register_date).getTime();
        });
      } else if (option == "Price (low to high)") { // Price (low to high)
        this.clothing.sort((a, b) => {
          return Number(a.price.replace(',', '')) - Number(b.price.replace(',', ''));
        });
      } else if (option == "Price (high to low)") { // Price (high to low)
        this.clothing.sort((a, b) => {
          return Number(b.price.replace(',', '')) - Number(a.price.replace(',', ''));
        });
      } else if (option == "Name A-Z") { // Name A-Z
        this.clothing.sort((a, b) => {
          return a.name.localeCompare(b.name);
        });
      } else if (option == "Name Z-A") { // Name Z-A
        this.clothing.sort((a, b) => {
          return b.name.localeCompare(a.name);
        });
      }
    },
  
    // 카테고리
    printBy(brand, low, high) {
      let temp = [];

      // 브랜드별 분류
      if (brand == "All") {
        temp = this.data;
      } else {
        this.data.forEach(element => {
          if (element.brand == brand) {
            temp.push({
              name: element.name,
              price: element.price,
              url: element.url,
              brand: element.brand,
              register_date: element.register_date
            })
          }
        });
      }

      this.clothing = []

      // 가격별 분류
      temp.forEach(element => {
        let price = Number(element.price.replace(',', ''));
        if (low <= price && price <= high) {
          this.clothing.push({
            name: element.name,
            price: element.price,
            url: element.url,
            brand: element.brand,
            register_date: element.register_date
          })
        }
      })

      this.totalClothing = this.clothing.length;
      this.clothingToShow = Math.min(this.loadDataCount, this.totalClothing);
    },
  },
};
</script>

<style>
.ul-clothing_list {
  list-style: none;
}

.li-clothing_list_item {
  float: left;
  margin: 0 20px 80px 0;
}

.div-clothing_list_item_wrapper {
  width: 345px;
  height: 345px;
}

.div-clothing_name {
  font-size: 17px;
  line-height: 23.8px;
  margin-top: 5px;
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.div-clothing_price {
  font-size: 16px;
  line-height: 16px;
  color: #666666;
  margin-top: 5px;
}

.div-load_more_btn_wrapper {
  margin: 40px 0 40px 0;
  clear: both;
}

.btn-load_more {
  background-color: black !important;
  color: white !important;
  border-radius: 0px !important;
  font-size: 17px;
  line-height: 23.8px;
  text-align: center;
  word-spacing: 0px;
  height: 43.8px;
  width: 316px;
  padding: 10px 20px 10px 20px;
}
</style>